.whole-chat {
    /* background-image: url('../assets/images/header_background_image.jpg'); */
    background-color: rgb(176, 125, 125);
    background-size: cover;  /* 화면을 덮도록 설정하며 이미지 비율 유지 */
    background-position: center;  /* 이미지 중앙에 위치 */
    background-repeat: no-repeat;  /* 이미지 반복 방지 */
    width: 100%;  
    /* display: flex; */
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden; 
    
}

.chat-page {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 40vh;
    justify-content: center;
    border-radius: 15px;
    /* 둥근 모서리 설정 */
    background-color: rgba(122, 111, 111, 0.7);
    /* 투명한 회색 배경 */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    /* 약간의 그림자 추가 (선택 사항) */
    margin-left: 20%;
    margin-right: 20%;
    margin-top: 15vh;
    margin-bottom: 15vh;
}


.history-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    /* 하단 정렬 */
    border-radius: 15px;
    /* 둥근 모서리 설정 */
    background: linear-gradient(to top, #a8edea 0%, #fed6e3 100%);
    box-shadow: 0 4px 8px rgba(255, 255, 255, 0.676);
    /* 약간의 그림자 추가 (선택 사항) */
    padding-bottom: 20px;
    margin-left: 25%;
    margin-right: 25%;
    margin-top: 50px;
    margin-bottom: 10px;
    /* 추가 스타일 */
    display: flex;
    align-items: center;
    justify-content: center;
}

.history-content {
    display: flex;
    justify-content: center;
    /* 수평 가운데 정렬 */
    align-items: center;
    /* 수직 가운데 정렬 (필요 시) */
    flex-direction: column;
    /* 아이템들을 세로로 쌓이게 함 (선택 사항) */
    text-align: center;
    /* 텍스트가 중앙 정렬되도록 설정 */
    min-height: 20vh;
}


.history-content-text {
    display: flex;
    /* Flexbox 사용 */
    justify-content: center;
    /* 수평 가운데 정렬 (필요 시) */
    align-items: center;
    /* 수직 가운데 정렬 */
    height: 100%;
    /* 컨테이너의 높이를 채워야 함 */
    text-align: center;
    /* 텍스트 가운데 정렬 */


    min-height: 10vh;
    margin-top: 20px;
    margin-bottom: 10px;
    color: white;
    /* 텍스트가 잘 보이도록 흰색 설정 */
    font-family: 'Dancing Script', cursive;
    /* Dancing Script 폰트 적용 */
}


.spinner {
    margint-top: 20px;
    border: 4px solid rgba(0, 0, 0, 0.1);
    width: 70px;
    height: 70px;
    border-radius: 50%;
    border-left-color: rgb(208, 10, 247);
    animation: spin 1s linear infinite;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}



.whole-chat::after {
    content: "";
    display: block;
    height: 20px;
    /* 원하는 높이만큼 설정하세요 */
}

.input-box {
    display: flex;
    align-items: center;
    /* margin-bottom: 10px; */
    width: 90%;
    /* input-box의 가로 크기 설정 */
    padding: 15px;

}


.text-input {
    min-height: 150px;
    padding: 10px;
    border-radius: 5px;
    border: 2px solid #ff0000;
    margin-right: 10px;
    flex: 1;
    resize: none;
    /* 사용자가 크기를 조절할 수 없도록 설정 (선택 사항) */
    overflow-y: auto;
    /* 스크롤 생성을 위해 자동 */
    line-height: 1.5;
    /* 줄 간격 설정 */
    font-size: 16px;
    /* 글자 크기 설정 */
    white-space: pre-wrap;
    /* 줄 바꿈 허용 */
}



.button-container {
    display: flex;
    justify-content: space-between;
    /* 버튼을 좌우로 배치 */
    gap: 10px;
    /* 버튼 사이에 간격 추가 */
    margin-bottom:20px;

}





.submit-button {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: white;
    cursor: pointer;
}

.bubble-container {
    width: 82%;
    /* 말풍선 컨테이너의 가로 크기 설정 */
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}


.bubble {
    display: inline-block;
    /* 텍스트 크기에 맞게 조정됨 */
    padding: 10px;
    /* 텍스트와 테두리 사이의 여백 */
    padding-bottom: 8px;
    border: 2px solid black;
    /* 테두리 설정 */
    border-radius: 5px;
    /* 둥근 모서리 설정 */
    background-color: #ffffff;
    /* 배경색 설정 */
    margin-bottom: 10px;
    margin-top: 10px;
}

.input-bubble {
    align-self: flex-end;
    /* 오른쪽 정렬 */
}

.bubble>p {
    margin: 0px;
}

.button-container {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-top: 20px;
}



.btn-outline-dark {
    margin-left: 10px;
}

.scale-up-hor-center {
    -webkit-animation: scale-up-hor-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
    animation: scale-up-hor-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}



