.speech-recognition {
    text-align: center;
    margin-top: 10px;
  }
  
  .popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .popup-content {
    background: white;
    padding: 20px;
    border-radius: 5px;
    width: 300px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    text-align: center;
    position: relative; /* 절대 위치 요소와 함께 사용 */
  }
  
  .close-button {
    position: bottom;
    top: 10px;
    right: 10px;
    background: transparent;
    border: none;
    font-size: 18px;
    cursor: pointer;
  }
  
  .close-button:hover {
    color: red;
  }
  
  .mic-icon {
    width: 20px;
    height: 20px;
    cursor: pointer;
    position: relative;
    display: inline-block;
    margin-bottom: 20px;
}