  /* Popup overlay */
  .popup-overlay {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.5);
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 1000;
  }

  /* Popup container */
  .popup-container {
      background-color: #fff;
      padding: 30px;
      border-radius: 10px;
      max-width: 600px;
      width: 100%;
      box-sizing: border-box;
      position: relative;
      text-align: center;
      font-size: 1.5em;
      display: flex;
      flex-direction: column;
      /* Flexbox column direction for vertical alignment */
      align-items: center;
      /* Center items horizontally */
  }

  /* Close button */
  .popup-close-button {
      position: absolute;
      top: 15px;
      /* Adjusted for larger container */
      right: 15px;
      background: transparent;
      border: none;
      font-size: 24px;
      /* Increased font size */
      cursor: pointer;
  }

  /* Buttons inside popup */
  .down-button {
      display: block;
      /* Make buttons block-level elements */
      margin-top: 15px;
      /* Increased spacing */
      padding: 15px 30px;
      /* Increased padding */
      font-size: 18px;
      /* Increased font size */
      cursor: pointer;
      margin: 10px;
      /* Increased margin */
      border-radius: 10px;
      /* More rounded corners */
      border: none;
      background-color: #ffa200;
      color: #fff;
      flex-direction: column;
      /* Flexbox column direction for vertical alignment */
      align-items: center;
      /* Center items horizontally */
  }

  .retry-button {
      display: block;
      /* Make buttons block-level elements */
      margin-top: 15px;
      /* Increased spacing */
      padding: 15px 30px;
      /* Increased padding */
      font-size: 18px;
      /* Increased font size */
      cursor: pointer;
      margin: 10px;
      /* Increased margin */
      border-radius: 10px;
      /* More rounded corners */
      border: none;
      background-color: #007bff;
      color: #fff;
  }