/* App 전체 스타일 */
.app-container {
  margin-top: 15px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;

}



/* 텍스트 컨테이너 스타일 */
.text-container {
  display: flex;
  gap: 15px;
}
.bottom-container{
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  height: 70px;
}

.top-container {
  display: block;
}

.group {
  display: flex;
  justify-content: center;
}
/* 클릭 가능한 텍스트 스타일 */
.clickable-text {
  display: inline;
  font-size: 18px;
  color: #000000;
  cursor: pointer;
  margin-right: 15px;
  transition: color 0.3s;
}

.clickable-text:hover {
  color: #0056b3;
}

.clickable-text:last-child {
  margin-right: 0; /* 마지막 요소에는 오른쪽 간격 제거 */
}

/* Modal 스타일 */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  width: 300px;
}

.close-link {
  margin-top: 20px;
  font-size: 16px;
  color: #dc3545;
  cursor: pointer;
  text-decoration: underline;
  transition: color 0.3s;
}

.close-link:hover {
  color: #c82333;
}

/* font */
span {
  font-family: "Anton", sans-serif;
  font-weight: 400;
  font-style: normal;
}
