/* 글자 fade inout */
.word-container {
    position: relative;
    font-size: 24px;
    font-weight: bold;
    color: black;
}


.word {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    /* 가운데 정렬 */
    opacity: 0;
    animation: fadeInOut 9s infinite;
}


.word:nth-child(1) {
    animation-delay: 0s;
}

.word:nth-child(2) {
    animation-delay: 3s;
}

.word:nth-child(3) {
    animation-delay: 6s;
}


@keyframes fadeInOut {

    0%,
    44%,
    100% {
        opacity: 0;
    }

    11.11%,
    22.22% {
        opacity: 1;
    }
}



/* header image */
.background-container {
    /* background-image: url('../assets/images/image2.jpg');  */
    background-color: bisque;
    background-size: cover;  /* 화면을 덮도록 설정하며 이미지 비율 유지 */
    background-position: center;  /* 이미지 중앙에 위치 */
    background-repeat: no-repeat;  /* 이미지 반복 방지 */
    width: 100%;  /* 가로 크기를 화면에 맞게 설정 */
    height: 90vw;  /* 높이를 화면 전체 높이에 맞게 설정 */
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;  /* 넘치는 내용 숨김 */
}


.masthead-content {
    color: black;  /* 텍스트 색상 */
    text-align: center;  /* 텍스트 가운데 정렬 */
    z-index: 1;
}

/* 배너 */
.marquee-container {
    width: 100vw;  /* 컨테이너의 가로 크기를 화면 전체 너비로 설정 */
    white-space: nowrap;  /* 텍스트가 한 줄로 표시되도록 설정 */
    box-sizing: border-box;
    position: absolute;  /* 위치를 절대적으로 설정 */
}

.marquee1-container {
    bottom: 33%;  /* 첫 번째 배너가 화면 아래에서 20% 위로 위치 */
}

.marquee2-container {
    bottom: 41%;  /* 두 번째 배너가 화면 아래에서 5% 위로 위치 */
}

.marquee {
    display: inline-block;  /* 텍스트가 줄바꿈 없이 이어지도록 설정 */
    animation: marquee 35s linear infinite;  /* 기존 방향 애니메이션 */
    font-size: 9rem;  /* 글자 크기 조정 */
    white-space: nowrap;  /* 텍스트가 한 줄로 유지되도록 설정 */
}

.marquee-reverse {
    display: inline-block;  /* 텍스트가 줄바꿈 없이 이어지도록 설정 */
    animation: marqueeReverse 35s linear infinite;  /* 반대 방향 애니메이션 */
    font-size: 9rem;  /* 글자 크기 조정 */
    white-space: nowrap;  /* 텍스트가 한 줄로 유지되도록 설정 */
}

@keyframes marquee {
    from { transform: translateX(-20%); }  /* 텍스트가 화면 오른쪽 밖에서 시작 */
    to { transform: translateX(-70%); }  /* 텍스트가 화면 왼쪽 밖으로 사라지도록 이동 */
}

@keyframes marqueeReverse {
    from { transform: translateX(-60%); }  /* 텍스트가 화면 왼쪽에서 시작 */
    to { transform: translateX(-10%); }  /* 텍스트가 화면 오른쪽으로 사라지도록 이동 */
}

.marquee p, .marquee-reverse p {
    display: inline-block;
    padding-right: 0;  /* 텍스트 사이에 간격 없이 연속적으로 표시되도록 설정 */

}


.anton-regular {
    font-family: "Anton", sans-serif;
    font-weight: 400;
    font-style: normal;
}




/* 배너 아래 div */


.content-box {
    position: relative;
    margin-top: 100vh;
    bottom: 30%;  /* 두 번째 배너 아래로 설정 */
    width: 100%;  /* 전체 너비 */
    display: flex;  /* 내부 박스를 나란히 배치하기 위한 Flexbox */
    justify-content: space-between;  /* 왼쪽과 오른쪽 박스를 양쪽으로 배치 */
    align-items: center;  /* 박스 내용 가운데 정렬 */
    text-align: center;  /* 텍스트 가운데 정렬 */
    color: black;  /* 텍스트 색상 */
    font-size: 1.5rem;  /* 텍스트 크기 */
}

/* 왼쪽 박스 */
.left-box {
    width: 70%;  /* 전체 너비의 48% 할당 */
    padding: 20px;  /* 여백 추가 */
    box-sizing: border-box;  /* 패딩 포함한 크기 계산 */
    margin-left: 10%;
    margin-bottom: 7%;
    text-align: left;  /* 텍스트를 왼쪽 정렬 */
    display: flex;
    flex-direction: column;  /* 내부 요소를 수직으로 나열 */
    align-items: flex-start;
}

/* 오른쪽 박스 */
.right-box {
    width: 25%;  /* 전체 너비의 48% 할당 */
    padding: 20px;  /* 여백 추가 */
    box-sizing: border-box;  /* 패딩 포함한 크기 계산 */
    margin: 10%;
    
}

.nanum-myeongjo-regular {
    font-family: "Nanum Myeongjo", serif;
    font-weight: 40;
    font-style: normal;
    font-size: 20px;
}


/* 로고 */
.rotate-logo {
    width: 300px; /* 로고 크기 설정 */
    height: auto; /* 자동 높이 조정 */
    animation: rotate-clockwise 6s linear infinite; /* 회전 애니메이션 */
}

@keyframes rotate-clockwise {
    from {
        transform: rotate(0deg); /* 0도에서 시작 */
    }
    to {
        transform: rotate(360deg); /* 360도 회전 */
    }
}

.download-btn {
  background-color: #8B4513; /* 버튼 배경색 */
  border: none; /* 테두리 제거 */
  color: white; /* 텍스트 색상 */
  padding: 15px 32px; /* 패딩 */
  text-align: center; /* 텍스트 정렬 */
  text-decoration: none; /* 텍스트 장식 제거 */
  display: inline-block; /* 인라인 블록으로 표시 */
  font-size: 20px; /* 폰트 크기 */
  margin: 4px 2px; /* 마진 */
  cursor: pointer; /* 마우스 커서 모양 */
  border-radius: 8px; /* 버튼 모서리 둥글게 */
  transition: background-color 0.3s ease; /* 배경색 전환 효과 */
}

.download-btn:hover {
  background-color: #a06b45; /* 마우스 오버 시 배경색 */
}








