.whole-chat { 
    overflow: hidden; /* 솜뭉치가 전체 영역을 벗어나지 않도록 설정 */
    z-index: 2;
}
.cotton {
    position: absolute;
    background: white;
    border-radius: 50%;
    opacity: 0.8;
    box-shadow: 0 0 10px rgba(255, 106, 0, 0.7);
    z-index: -1;
}

@keyframes fall {
    0% {
        transform: translateY(-100px) scale(0.5);
        opacity: 0.8;
    }

    100% {
        transform: translateY(100vh) scale(1);
        opacity: 0;
    }
}